.isotope-filter-selection {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  padding: 6rem;
}

.isotope-filter-selection .select-box img {
  display: block;
  width: 60px;
  height: 60px;
  object-fit: cover;
  margin: 3rem auto;
  margin-top: 0;
}

.isotope-filter-selection select {
  border-radius: 6px;
  font-family: var(--font3);
  font-size: 1.5rem;
  text-transform: uppercase;
  padding: 4px 8px;
  background: var(--c1);
  border: 1px solid #ddd;
  color: #111;
  text-align: center;
}

.member-list-grid {
  width: 1200px;
  flex: 1;
  margin-left: -75px;
  margin-bottom: -75px;
  border: 75px solid #111;
  border-top-width: 125px;
  border-bottom-width: 125px;
  background: #111;
  min-height: 20px !important;
  max-height: 1050px !important;
  overflow-x: hidden;
  overflow-y: scroll;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.member-list-grid .item {
  width: 330px;
  margin-right: 10px;
  margin-left: 10px;
  margin-bottom: 20px;
  border-radius: 3px;
  padding: 1rem;
  background: linear-gradient(120deg, rgba(var(--mclr), 1) 0%, rgba(var(--hclr), 1) 50%, rgba(var(--gclr), 1) 100%);
}

.member-list-grid .item .item-inner-wrap {
  width: 100%;
  border-radius: 3px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.member-list-grid .item .item-inner-wrap .item-player {
  border-top: 1px solid #111;
  width: 100%;
  padding: 1rem;
  display: flex;
  align-items: center;
  flex-flow: row wrap;
  justify-content: space-between;
  font-family: var(--font4);
  font-size: 12px;
  text-transform: lowercase;
}

.member-list-grid .item .item-inner-wrap .item-player .group {
  font-weight: bold;
  background-image: var(--ani);
  width: 35px;
  height: 35px;
  background-size: cover;
}

.member-list-grid .item .item-inner-wrap .item-av {
  width: 175px;
  height: 175px;
  border: 1px solid #111;
  border-radius: 100%;
  padding: 1rem;
}

.member-list-grid .item .item-inner-wrap .item-av img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 100%;
}

.member-list-grid .item .item-inner-wrap .item-name {
  margin-top: 3rem;
  font-family: var(--font3);
  font-size: 2rem;
  text-transform: uppercase;
  margin-bottom: 2rem;
  color: #111;
}

.member-list-grid .item .item-inner-wrap .item-name a {
  display: inline-block;
  font-family: inherit;
  border-bottom: 1px solid #111;
}

.member-list-grid .item .item-inner-wrap .guide-link {
  border-radius: 3px;
  padding: 1rem;
  background: #111;
  color: #fff;
  display: block;
  width: 100%;
}

.member-list-grid .item .item-inner-wrap .guide-link:first-of-type {
  margin-bottom: 1rem;
}

.member-list-grid .item .item-inner-wrap .item-info {
  padding: 4rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: var(--font3);
  font-size: 1.25rem;
  text-transform: uppercase;
}